html,body,#root,.App{
  width:100%;height: 100%;
}
body {
  position: absolute;
  margin: 0;
  background: rgb(169,108,210);
  background: linear-gradient(90deg, rgba(169,108,210,1) 15%, rgba(124,182,76,1) 61%, rgba(69,211,252,1) 95%);
  font-family: 'Oxygen-Regular', 'Segoe UI';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
theme-dark {
  --dark-text: #292929;
  --light-text: #F9F8F8;  
  --dark-background: #2F4550;
  --light-background: #586F7C;
  --accent: #B8DBD9;
  --button-border: #B8DBD9;
}

.theme-light {
  --dark-text: #5E4B56;
  --light-text: #5E4B56;
  --dark-background: #DBE7E4;
  --light-background: #EDDCD2;
  --accent: #DBE7E4;
  --button-border: #5E4B56;
}
html, #root {
  background-color: var(--dark-background);
  color: var(--dark-text);
}

nav {
  background-color: var(--dark-background);
  color: var(--light-text);
}

#root {
  background-color: var(--light-background);
}

.selectedRow{
  background-color: lightseagreen;
}

.mytable>thead>tr>th{
  cursor: pointer;
}


.mytable>thead>tr .searchInputCss{
  padding: 0.5rem 0 0.5rem 0.5rem;

}
.mytable>thead>tr .searchInputCss span {
  width: 100%;
}
.mytable>thead>tr .searchInputCss input {
  min-height: 2.6rem;
  padding: 0.3em;
  font-size: large;
  border-radius: 0.3em;
  border: #999 solid 1px;
  border-radius: 0.3em;
  color: rgb(170, 114, 114);
}
.mytable>thead>tr .searchInputCss input:focus {
  outline: none !important;
  box-shadow: 0 0 0.3em #719ECE;
}
.mytable>tfoot>tr>td>a {
  cursor: pointer;
  padding: 0.4em;
  margin-right: 0.1em;
}

.mytable .selectedRow {
  background-color: aliceblue!important;
}
.mytable .selectedPage{
  text-decoration : underline;
}

.mytable .pagerDiv {
display: inline-block;
padding: 0.5em;
background-color: #f7f7f7;
min-width: 230px;
}
.mytable .pagerDiv button  {
  float: left;
  font-size: 1.3em;
  border: 0.05em solid grey;
  background: none;
  box-shadow: none;
  border-radius: 0px;
  background-color: #f9f9f9;
  color:#999;
  padding: 0.1em 0.3em;
}


.mytable .pagerDiv  input {
  float: left;
  width:3em;
  line-height: 1.5em;
  padding-left: 0.3em;
  color:#999;
}

.mytable .pagerDiv .pageCounter {
position: relative;
top:0.25em;
margin-left:0.5em;
line-height: 1.5em;
background-color: #fff;
padding: 0.3em;
float: left;
font-style: italic;
font-size: 0.7em;
}

.App {
  text-align: center;
}

